import axios from 'axios';
import { getClientParams, getClientParamsFromUrl, getClientParamsTest } from '@src/utils/app';
import { jsonp } from '@ifeng/ui_base';
// import Grounds from 'ThreeTerminal_base/grounds';
export const request = axios.create();
// eslint-disable-next-line no-undef
const clientParams = window.grounds && NODE_ENV !== 'development' ? getClientParamsFromUrl() : getClientParamsTest();

request.interceptors.request.use(
    (config) => {
        return { ...config, params: { ...config.params, ...clientParams } };
    },
    (err) => {
        return Promise.reject(err);
    },
);
// 
export const getHotRankApi = async () => {
    // 
    const local = 'http://172.30.21.15:8080/phoenixtv/search/hotlist';
    const testUrl = 'http://test.nine.ifeng.com/phoenixtv/search/hotlist';
    const offline = 'https://nine.ifeng.com/phoenixtv/search/hotlist';
    // eslint-disable-next-line init-declarations
    let data;

    // eslint-disable-next-line no-undef
    switch (NODE_ENV) {
        case 'development': // 
            // return await axios.get('https://api.ifengcloud.ifeng.com/mock/1755/communityInfoNav');
            // eslint-disable-next-line no-case-declarations
            data = await jsonp(`${testUrl}`, {
                data: {
                    ...getClientParamsTest(),
                },
                jsonpCallback: 'getPostInfo',
                cache: false,
            });

            console.log(data);

            return data.data;
        case 'staging': // 
            data = await Grounds.nativeRequest({
                url: testUrl,
                type: 'get',
            });

            return data.data;
        default:
            data = await Grounds.nativeRequest({
                url: offline,
                type: 'get',
            });

            return data.data;
    }
};
// 
export const getSearchResultApi = async (keyword, page = 1) => {
    console.log(keyword);
    // eslint-disable-next-line init-declarations
    let data;
    const local = 'http://172.30.21.15:8080/searchH5VideoList';
    const testUrl = 'http://test.nine.ifeng.com/searchH5VideoList';
    const offline = 'https://nine.ifeng.com/searchH5VideoList';

    // eslint-disable-next-line no-undef
    switch (NODE_ENV) {
        case 'development': // 
            // return await axios.get('https://api.ifengcloud.ifeng.com/mock/1755/communityInfoNav');
            // eslint-disable-next-line no-case-declarations
            data = await jsonp(`${testUrl}`, {
                data: {
                    ...getClientParamsTest(),
                    k: keyword,
                    page,
                },
                jsonpCallback: 'getPostInfo',
                cache: false,
            });

            return data;
        case 'staging': // 
            data = await Grounds.nativeRequest({
                url: testUrl,
                getParams: JSON.stringify({ k: keyword, page }),
                type: 'get',
            });

            return data;
        default:
            data = await Grounds.nativeRequest({
                url: offline,
                getParams: JSON.stringify({ k: keyword, page }),
                type: 'get',
            });

            return data;
    }
};
