import React, { useReducer, useContext, useEffect } from 'react';
import styles from './index.css';
import {
    ADD_SEARCH_BOX_LIST,
    CHANGE_INPUT_VALUE,
    CHANGE_SEARCH_BOX_LIST,
    CLEAR_SEARCH_BOX_LIST,
    SEARCH_HISTORY,
    SearchBoxData,
    SHOW_TIP,
} from '@src/mobile/search/layout/store';
// import { getSearchResultApi } from '@src/mobile/search/service';
import { Tip } from '@src/constants';
// import { Grounds } from '@ifeng/client_grounds';
const backImg = 'https://x0.ifengimg.com/ucms/2022_12/BAF727636C099486435E9B4A90987917304C98CC_size3_w180_h180.png';
const searchImg = 'https://x0.ifengimg.com/ucms/2022_12/C204D740F133DB4EB2CF91A84C356D67F0DAE475_size5_w96_h96.png';

const SearchBox = () => {
    const { data, dispatch } = useContext(SearchBoxData);
    const clickSearch = async () => {
        let inputValue = data.inputValue;

        inputValue = inputValue.replace(/\s*/g, '');
        if (!inputValue) {
            inputValue = data.firstHotRankItem.title;
        }
        dispatch({
            type: SEARCH_HISTORY,
            data: { searchValue: inputValue, page: 1 },
        });
        sendPage(inputValue);
        try {
            // const res = await getSearchResultApi(inputValue);

            // console.log(res);
            // if (res.item?.length > 0) {
            //     dispatch({
            //         type: CHANGE_SEARCH_BOX_LIST,
            //         data: { searchBoxList: [...res.item], showTip: Tip.SUCCESS, totalPage: res.totalPage },
            //     });
            // } else {
            //     dispatch({
            //         type: CHANGE_SEARCH_BOX_LIST,
            //         data: { searchBoxList: [], showTip: Tip.NOSEARCHDATA, totalPage: 0 },
            //     });
            // }
        } catch (e) {
            console.log(e);
            dispatch({
                type: SHOW_TIP,
                data: { showTip: Tip.NOINTERNET, clickSearch, totalPage: 0 },
            });
        }
    };

    useEffect(
        () => {
            if (data.searchBoxList.length > 0) {
                window.addEventListener('scroll', handleScroll);

                return () => window.removeEventListener('scroll', handleScroll);
            }
        },
        [data.searchBoxList],
    );
    const handleScroll = async () => {
        console.log(data.page, data.totalPage);
        if (data.page >= data.totalPage) {
            return;
        }
        const scrollTop = document.body.scrollTop || document.documentElement.scrollTop;

        if (window.innerHeight + scrollTop < document.documentElement.offsetHeight) return;
        dispatch({
            type: SHOW_TIP,
            data: {
                showTip: Tip.LOADING,
            },
        });
        const res = await getSearchResultApi(data.searchValue, data.page + 1);

        if (res?.item?.length > 0) {
            dispatch({
                type: ADD_SEARCH_BOX_LIST,

                data: { searchBoxList: res.item, page: data.page + 1, showTip: Tip.SUCCESS },
            });
        } else {
            dispatch({
                type: SHOW_TIP,

                data: { showTip: Tip.SUCCESS },
            });
        }

        console.log('Fetch more list items!');
    };
    const inputValue = e => {
        dispatch({
            type: CHANGE_INPUT_VALUE,
            data: { inputValue: e.target.value },
        });
    };
    const sendPage = key => {
        // Grounds.pageStat({
        //     behavior: 'page',
        //     id: `phmoviesrh_${key}`,
        //     type: 'other',
        //     ref: 'phmoviesrh',
        //     pagetag: ''
        // });
    };
    const goBack = () => {
        if (data.searchBoxList.length > 0 || data.showTip === Tip.NOSEARCHDATA) {
            dispatch({
                type: CLEAR_SEARCH_BOX_LIST,
            });
        } else if (window.grounds) {
            window.grounds.close();
        }
    };

    return (
        <div className={styles.searchBox}>
            <img
                onClick={() => {
                    goBack();
                }}
                className={styles.backImg}
                src={backImg}
            />
            <div className={styles.searchMain}>
                <img className={styles.searchImg} src={searchImg} />
                <input
                    onKeyDown={e => {
                        if (e.keyCode === 13) {
                            clickSearch();
                        }
                    }}
                    onInput={e => {
                        inputValue(e);
                    }}
                    value={data.inputValue}
                    placeholder={data.firstHotRankItem.title || ''}
                    className={styles.searchInput}
                />
            </div>

            <div onClick={clickSearch} className={styles.searchText}>
                
            </div>
        </div>
    );
};

export default SearchBox;
