import React, { useContext, useEffect, useState } from 'react';
import styles from './index.css';
import { FIRST_HOT_RANK, SearchBoxData, } from '@src/mobile/search/layout/store';
import { getHotRankApi } from '@src/mobile/search/service/index';
import PropTypes from 'prop-types';
import { newGoPage } from '@src/utils';
import { detailPageUrl, Tip } from '@src/constants';
// import { Grounds } from '@ifeng/client_grounds';
import errorBoundary from "@src/components/errorBoundary"
const orderImg = [
    'https://x0.ifengimg.com/ucms/2022_12/B10E33EC13B47DE7D64F572F82D960A6FE9EBE19_size1_w48_h96.png',
'https://x0.ifengimg.com/ucms/2022_12/279EFBD0B3A9301D0B475D501999D0DDACA1D4B1_size4_w48_h96.png',
    'https://x0.ifengimg.com/ucms/2022_12/A3B01DC02933A626C5E1A54EB639DCD8FDB4776B_size5_w48_h96.png',
'https://x0.ifengimg.com/ucms/2022_12/BF8CFC51FA74B892C0C2EEED05DA326BFA8A9959_size2_w48_h96.png',
'https://x0.ifengimg.com/ucms/2022_12/F65561A9B46887C21ADCAD49D640313B02277257_size3_w48_h96.png',
'https://x0.ifengimg.com/ucms/2022_12/8D4004FC2B2E634DFA2B6712CA51C3F8D2886E6A_size3_w48_h96.png',
'https://x0.ifengimg.com/ucms/2022_12/064665FA03DE08094BD602B796589A43CB54B159_size2_w48_h96.png',
'https://x0.ifengimg.com/ucms/2022_12/F38C7BA631FC8EEFABF109BB12D6E9721E7A1AA5_size4_w48_h96.png'
];
const HotRank = () => {
  const [rankData, setRankData] = useState([]);
  const { data, dispatch } = useContext(SearchBoxData);
  const getHotRankData = async() => {
    const res = await getHotRankApi();

    if (res.items && res.items.length > 0) {
      setRankData(res.items);
      dispatch({
        type: FIRST_HOT_RANK,
        data: { ...data, firstHotRankItem: res.items[0], searchValue: res.items[0].title },
      });
    }

  };

  useEffect(() => {
    getHotRankData();
  }, []);

return (
    <>
      {
          (data.showTip === Tip.SUCCESS && !data.searchBoxList?.length > 0) && <div className={styles.hotRank}>
          <div className={styles.text}></div>
          {rankData?.length > 0 && rankData.map((item, index) => {
            return <BoxItem key={index} item={item} index={index} />;
          })}
        </div>
      }

    </>
  );
};
const BoxItem = ({ index, item }) => {
  const goPage = () => {
    Grounds.dispatch({
      link: {
        type: 'web',
        url: `${detailPageUrl}/${item.id.replace('ucms_', '')}?id=phmovie_${item.id.replace('ucms_', '')}&type=phmovie&ref=phmoviesrh&showtype=phhotposter&rnum=${index}&needpage=1&immersion=true&ifengsp=1&isFull=0`
      },
      pageLogParams: {
        ref: 'phmoviesrh',
        rnum: index,
        showtype: 'phhotposter',
        type: 'phmovie',
        id: `phmovie_${item.id.replace('ucms_', '')}`
      }
    });
  };

return (<div onClick={goPage} className={styles.boxItem}>
    <img className={styles.boxItemImg} src={orderImg[index]} alt="" />
    <div className={styles.boxItemText}>{item.title || ''}</div>
  </div>);
};

BoxItem.propTypes = {
  item: PropTypes.object,
  index: PropTypes.number,
};
export default errorBoundary(HotRank);
