import React, { createContext } from 'react';
import { Tip } from '@src/constants';
export const SearchBoxData = createContext({});
export const FIRST_HOT_RANK = 'FIRST_HOT_RANK';
export const CHANGE_INPUT_VALUE = 'CHANGE_INPUT_VALUE';
export const SEARCH_HISTORY = 'SEARCH_HISTORY';
export const DEL_SEARCH_HISTORY = 'DEL_SEARCH_HISTORY';
export const DEL_ONE_SEARCH_HISTORY = 'DEL_ONE_SEARCH_HISTORY';
export const ADD_SEARCH_BOX_LIST = 'ADD_SEARCH_BOX_LIST';
export const CLEAR_SEARCH_BOX_LIST = 'CLEAR_SEARCH_BOX_LIST';
export const CHANGE_SEARCH_BOX_LIST = 'CHANGE_SEARCH_BOX_LIST';
export const SHOW_TIP = 'SHOW_TIP';
export const reducer = (state, action) => {
  switch (action.type) {
    case SHOW_TIP:
      console.log(1111);

      return {
        ...state,
        clickSearch: action.data.clickSearch,
        showTip: action.data.showTip,
      };
    case ADD_SEARCH_BOX_LIST:
      // eslint-disable-next-line no-case-declarations
       let searchBoxList = state.searchBoxList.concat(action.data.searchBoxList);
      // eslint-disable-next-line no-case-declarations
      const obj = {};

      searchBoxList = [...searchBoxList].reduce((item, next) => {
        // eslint-disable-next-line no-unused-expressions
        obj[next.id] ? '' : (obj[next.id] = true && item.push(next));

        return item;
      }, []);

return { ...state, ...action.data, searchBoxList };
    case CHANGE_SEARCH_BOX_LIST:
      return { ...state, ...action.data };
    case FIRST_HOT_RANK:
      return { ...state, ...action.data };
    case CHANGE_INPUT_VALUE:
      return { ...state, ...action.data };
    case CLEAR_SEARCH_BOX_LIST:
      return { ...state, searchBoxList: [], showTip: Tip.SUCCESS };
    case SEARCH_HISTORY:
      if (action.data.searchValue) {
        const history = state.searchHistory;

        if (state.searchHistory.includes(action.data.searchValue)) {
         history.splice(state.searchHistory.indexOf(action.data.searchValue), 1);

        }
        window.localStorage.setItem('searchHistory', JSON.stringify([action.data.searchValue].concat(history).slice(0, 10)));

        return { ...state, ...action.data, searchHistory: [action.data.searchValue].concat(history).slice(0, 10) };
      } else {
        return { ...state, ...action.data, searchHistory: (state.searchHistory).slice(0, 10) };

      }

    case DEL_SEARCH_HISTORY:
      window.localStorage.removeItem('searchHistory');

      return { ...state, ...action.data, searchHistory: [] };
    case DEL_ONE_SEARCH_HISTORY:
      // eslint-disable-next-line no-case-declarations
      const newSearchHistory = state.searchHistory;

      newSearchHistory.splice(action.payload.index, 1);
      window.localStorage.setItem('searchHistory', JSON.stringify(newSearchHistory));

return { ...state, ...action.data, searchHistory: newSearchHistory };
    default:
      return state;
  }
};
